"use client";

import "./globals.css";
import type { Metadata } from "next";
import { useState, useEffect, useRef } from "react";
import { usePathname } from "next/navigation";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";

function LoadingScreen() {
  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-white/90 backdrop-blur-md">
      <div className="w-16 h-16 border-4 border-sky-600 border-t-transparent rounded-full animate-spin"></div>
    </div>
  );
}

export const metadata: Metadata = {
  title: "Dream Travels | Explore the World",
  description: "Travel agency website built with Next.js and Tailwind CSS",
};

export default function RootLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const [loading, setLoading] = useState(false);
  const prevPathname = useRef(pathname);

  useEffect(() => {
    if (prevPathname.current !== pathname) {
      // Delay state update to avoid synchronous setState
      const startLoader = setTimeout(() => setLoading(true), 50);

      const stopLoader = setTimeout(() => setLoading(false), 400);

      prevPathname.current = pathname;

      return () => {
        clearTimeout(startLoader);
        clearTimeout(stopLoader);
      };
    }
  }, [pathname]);

  return (
    <html lang="en">
      <body className="font-sans relative">
        {loading && <LoadingScreen />}
        <Navbar />
        <main className="pt-20 min-h-screen transition-opacity duration-500 ease-out">
          {children}
        </main>
        <Footer />
      </body>
    </html>
  );
}
